@import url("https://fonts.googleapis.com/css?family=Josefin+Sans&display=swap");
.SectionCard {
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.SectionCard .section-title {
  position: absolute;
  top: 100px;
  left: 0;
  transform: rotateZ(-90deg);
}
.SectionCard .Arrow {
  flex-shrink: 0;
}
.SectionCard .carrot {
  position: absolute;
  height: 100%;
  right: 10px;
}
.SectionCard .container {
  position: relative;
  display: flex;
  font-family: titleFont;
  overflow: hidden;
  width: 800px;
}
.SectionCard .container .slider {
  height: 100%;
  transition: transform 0.5s;
  display: flex;
}
.SectionCard .container .slider .slide {
  display: flex;
  align-items: center;
  width: 800px;
}
.SectionCard .container .slider .slide .content {
  flex: 1 0 0;
  display: flex;
  flex-direction: column;
  height: 100%;
}
.SectionCard .container .slider .slide .content .Pixi {
  flex-shrink: 0;
}
.SectionCard .container .slider .slide .content .slide-content {
  flex: 1 0 0;
  font-family: 'Josefin sans', sans-serif;
}
.SectionCard .container .slider .slide .content .slide-content .white-back-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.SectionCard .container .slider .slide .content .slide-content .white-back-container .white-back {
  background: #fff;
  color: #000;
  padding: 10px;
  box-sizing: border-box;
}
.Pixi {
  height: 100px;
  width: 550px;
  overflow: hidden;
}
.Arrow {
  height: 80px;
  width: 80px;
  fill: #fff;
  cursor: pointer;
  z-index: 2;
}
.Arrow:hover {
  fill: #4682b4;
}
.Arrow.right {
  transform: rotateZ(180deg);
}
.modal-wrapper {
  position: fixed;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: transform transitionTime;
  z-index: 1;
  background: rgba(255,255,255,0.5);
}
.modal-wrapper .content {
  position: relative;
  background: #fff;
  padding: 20px;
  border-radius: 3px;
  max-width: 90vw;
}
.modal-wrapper .content .CrossButton {
  position: absolute;
  top: 10px;
  right: 10px;
}
.BandModal .band-name {
  font-family: bandFont;
  font-size: 3em;
}
.BandModal .desc {
  width: 60vw;
}
.CrossButton {
  height: 20px;
  width: 20px;
  cursor: pointer;
}
.CrossButton:hover .cross-button-svg {
  fill: #f00;
}
.CrossButton .cross-button-svg {
  height: 100%;
  width: 100%;
}
.img-modal {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background: rgba(255,255,255,0.5);
  display: flex;
  align-items: center;
  z-index: 2;
}
.img-modal .arrow-container {
  flex-grow: 1;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.img-modal .arrow-left,
.img-modal .arrow-right {
  width: 5vw;
}
.img-modal .img-container {
  position: relative;
  border: 10px solid #fff;
}
.img-modal .img-container .CrossButton {
  position: absolute;
  top: -30px;
  right: -30px;
}
.img-modal .img-container .curr-img {
  max-height: 80vh;
  max-width: 80vw;
}
.Gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-left: 20px;
  margin-right: 20px;
  margin-top: 20px;
}
.Gallery .cell {
  height: 20vw;
  width: 20%;
  cursor: pointer;
  box-sizing: border-box;
  border: 3px solid transparent;
}
.Gallery .cell:hover {
  border: 3px solid #fff;
}
.Gallery .cell .thumb {
  object-fit: cover;
  height: 100%;
  width: 100%;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
html,
#root {
  overflow-x: hidden;
}
@font-face {
  font-family: titleFont;
  src: url("./static/Borgens Burlesque.ttf");
}
@font-face {
  font-family: bandFont;
  src: url("./static/Hasthon.ttf");
}
.hero {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
.hero .top-text {
  position: absolute;
  top: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 50%;
}
.hero .top-text .date,
.hero .top-text .edition {
  flex: 1 0 0;
  text-align: center;
  font-family: titleFont;
  font-size: 6em;
  color: #fff;
}
.hero .top-text .logo {
  height: 70%;
}
.hero .sky {
  position: absolute;
  width: 100vw;
  height: 100vh;
  background: radial-gradient(circle at bottom center, #e1db08 12%, #86255e 100%);
  z-index: -3;
}
.hero .sun {
  position: absolute;
  bottom: -5vh;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 50%;
  height: 40vh;
  width: 40vh;
  background: linear-gradient(96deg, #eee808 25%, #f17206 81%);
  z-index: -2;
}
.man-island {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  height: 247vh;
  z-index: -1;
}
.sea {
  height: 600vh;
  background: linear-gradient(0deg, rgba(29,69,97,0.4) 0%, rgba(17,48,89,0.4) 100%);
}
.sea .sponsors {
  position: absolute;
  bottom: 0;
  width: 100%;
  background: #fff;
  display: flex;
  justify-content: space-between;
  padding: 10px;
}
.sea .sponsors .sponsor {
  height: 80px;
}
.bands {
  display: flex;
  padding: 5px;
  align-content: center;
  flex-wrap: wrap;
}
.bands .band-card {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 100px;
  width: 100px;
  background: rgba(34,34,118,0.8);
  margin-right: 5px;
  margin-bottom: 5px;
  transition: all 0.3s;
  cursor: pointer;
}
.bands .band-card .name {
  font-family: bandFont;
  color: #fff;
}
.bands .band-card:hover .name {
  color: #e30077;
}
.tariff {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.tariff .pricing-cards {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  flex-wrap: wrap;
}
.tariff .pricing-cards .pricing-card {
  flex: 1 0 0;
  display: flex;
  flex-direction: column;
  align-content: space-between;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  background: rgba(255,255,255,0.7);
  padding-top: 2em;
  padding-bottom: 2em;
  white-space: nowrap;
}
.tariff .pricing-cards .pricing-card:last-of-type {
  margin-right: 0;
}
.tariff .pricing-cards .pricing-card .header {
  font-size: 1.5em;
}
.tariff .pricing-cards .pricing-card .icon {
  height: 50px;
}
.tariff .camping-stuff {
  display: none;
  width: 100%;
  background: rgba(255,255,255,0.7);
  display: flex;
  padding: 10px;
  margin-bottom: 20px;
  box-sizing: border-box;
}
.tariff .camping-stuff .left {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.tariff .camping-stuff .left .header {
  font-size: 1.5em;
}
.tariff .camping-stuff .left .icon {
  width: 60%;
}
.tariff .camping-stuff .right {
  margin-left: 20px;
  flex-grow: 1;
}
.tariff .coming-soon {
  width: 100%;
  font-size: 1.4em;
  box-sizing: border-box;
}
.story,
.project,
.volunteering {
  color: #fff;
  font-size: 1.2em;
}
@media screen and (max-width: mobileBreakPoint) {
  .Tickets .content {
    width: 100vw;
  }
  .Tickets .camping-stuff {
    flex-direction: column;
  }
  .Tickets .camping-stuff .icon {
    max-width: 50px;
  }
}
